import React, {Component} from 'react'
import {connect} from 'react-redux'
import { Input, Button } from 'antd';
import {actionCreators} from './store'
import {ContentWapper,ContentBox,FixBox,FixInputBox,FixOutputBox,FixBtn,TipsWapper} from './style'

class TextSegmentation extends Component{
    render(){
        const { textSplitFixResult,handleTextSplitFix } = this.props;
        return(
            <ContentWapper>
                <ContentBox>
                    <TipsWapper>
                        对中文语音文本进行智能分割，调加换行符号。
                    </TipsWapper>
                    <FixBox>
                        <FixInputBox>
                            <Input.TextArea allowClear ref={(input)=>{this.needFixText = input}} style={{ width: '100%' }} rows={15} placeholder="在此输入需要文本分割的语句" />
                        </FixInputBox>
                        <FixOutputBox>
                            <Input.TextArea disabled  value={textSplitFixResult} style={{ width: '100%' }} rows={15} placeholder="文本分割结果" />
                        </FixOutputBox>
                        <FixBtn>
                            <Button type="primary" onClick={()=>handleTextSplitFix(this.needFixText.state.value)}>智能分割</Button>
                        </FixBtn>
                    </FixBox>
                </ContentBox>
            </ContentWapper>
        )
    }
}

const mapStateToProps = (state)=>{
    return {
        textSplitFixResult: state.getIn(['textSplit','textSplitFixResult'])
    }
}

const mapDispatch = (dispatch) =>({
    handleTextSplitFix(text){
        dispatch(actionCreators.handleTextSplitFix(text))
    }
})

export default connect(mapStateToProps,mapDispatch)(TextSegmentation);